<template>
  <div class="flex flex-column tab-content">
    <yl-form-collapse collapse-position="right" collapse-height="42px">
      <template slot="form">
        <yl-form
          v-model="form"
          size="mini"
          v-bind="formProps"
        />
      </template>
      <template slot="collapse">
        <el-row class="flex flex-row justify-end">
          <el-button
            size="small"
            type="primary"
            @click="onQuery"
          >
            查询
          </el-button>
          <el-button
            size="small"
            @click="onReset"
          >
            清除
          </el-button>
        </el-row>
      </template>
    </yl-form-collapse>
    <div class="btn_groups">
      <el-button
        size="small"
        type="primary"
        @click="handleAdd"
      >
        新增
      </el-button>
    </div>
    <yl-table v-loading="loading.fetchData" v-bind="tableProps">
      <template slot="body(shippingOrder)" slot-scope="scope">
        <span class="hover_green_wj">{{
          scope.row.shippingOrder
        }}</span>
      </template>
      <template slot="body(action)" slot-scope="{ row, index }">
        <el-button
          type="text"
          @click="handleEdit(row)"
        >
          编辑
        </el-button>
        <el-button
          v-if="row.status===true"
          type="text"
          @click="handleEnable(row, false)"
        >
          停用
        </el-button>
        <el-button
          v-else
          type="text"
          @click="handleEnable(row, true)"
        >
          启用
        </el-button>
        <el-button
          type="text"
          @click="handleDel(index)"
        >
          删除
        </el-button>
        <el-button
          type="text"
          @click="handleView(row)"
        >
          查看流程
        </el-button>
      </template>
    </yl-table>
    <addOrEditDialog ref="addOrEditDialog" @success="handleSearch" />
  </div>
</template>

<script>
import searchPageMixin from '@thales/search-page';
// import { fetchPage } from '@/api/bms/fetchPage';
import loadingMixin from '@thales/loading';
import { formFields, tableFields } from './field';
import addOrEditDialog from './addOrEditDialog.vue';

export default {
  name: 'FunManage',
  components: {
    addOrEditDialog,
  },
  mixins: [searchPageMixin, loadingMixin],
  data() {
    return {
      form: {},
      tableData: [{}, { flowName: 'xxxx' }],
      tableHeight: '60vh',
      shippingOrderDetails: {
        visible: false,
        item: {},
      },
      loading: {
        fetchData: false,
      },
    };
  },
  computed: {
    formProps() {
      return {
        fields: formFields,
        fieldCol: {
          xl: 6,
          lg: 6,
          md: 8,
        },
        wrapperWidth: '80%',
        total: 10,
        nextFields: [
          {
            key: 'funName',
            nativeOn: {
              keyup: this.handleKeyUpQuery,
            },
            on: {
              clear: () => {
                this.onQuery();
              },
            },
          },
          {
            key: 'flowName',
            nativeOn: {
              keyup: this.handleKeyUpQuery,
            },
            on: {
              clear: () => {
                this.onQuery();
              },
            },
          },
        ],
      };
    },
    tableProps() {
      return {
        autofit: false,
        tableHeaderKey: 'funManage',
        height: this.tableHeight,
        fields: tableFields,
        border: true,
        total: this.total,
        data: this.tableData,
        pageSizeKey: 'size',
        currentKey: 'page',
        pageParam: {
          size: 10,
          page: 1,
        },
        ...this.mixinTableProps,
      };
    },
  },
  mounted() {
    setTimeout(() => {
      this.tableHeight = 'calc(100vh - 290px)';
    }, 50);
  },
  methods: {
    handleView() {
      window.open('#/dag?nodeId=1');
    },
    handleEnable(row, type) {
      const rowData = row;
      rowData.status = type;
      this.tableData.splice(this.tableData.indexOf(row), 1, rowData);
      this.onQuery();
    },
    handleDel(index) {
      this.$confirm('确定删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        this.tableData.splice(index, 1);
      });
    },
    handleSearch(row) {
      this.tableData.push(row);
    },
    handleAdd() {
      this.$refs.addOrEditDialog.show();
    },
    handleEdit(row) {
      this.$refs.addOrEditDialog.showEdit(row);
    },

    async fetchData(form) {
      const params = { ...form };
      // Object.keys(params).forEach((key) => {
      //   if (isEmpty(params[key])) {
      //     delete params[key];
      //   }
      // });
      const { page, size, ...rest } = params;
      console.log(rest);
      console.log(this.tableData);
      // const { total, records } = await fetchPage(
      //   { ...rest },
      //   {
      //     page: params.page,
      //     size: params.size,
      //   },
      // );
      // this.tableData = records;
      this.total = this.tableData.length;
    },
    handleKeyUpQuery({ keyCode }) {
      if (keyCode === 13) {
        this.onQuery();
      }
    },

  },
};
</script>

