<template>
  <el-dialog
    v-dialogDrag
    :title="title"
    :visible.sync="visible"
    width="30%"
    append-to-body
    :close-on-click-modal="false"
    @close="hide"
  >
    <div class="dialog_content adjust-box">
      <el-form
        ref="form"
        :model="form"
        label-width="120px"
        :rules="rules"
        label-position="right"
      >
        <el-form-item label="功能名称" prop="funName">
          <el-input
            v-model.trim="form.funName"
            placeholder="请输入功能名称"
            maxlength="30"
          />
        </el-form-item>
        <el-form-item label="归属应用" prop="appName">
          <el-input
            v-model.trim="form.appName"
            placeholder="请输入应用名称"
            maxlength="30"
          />
        </el-form-item>

        <el-form-item label="流程名称" prop="flowName">
          <el-input
            v-model.trim="form.flowName"
            placeholder="请输入流程名称"
            maxlength="30"
          />
        </el-form-item>
        <el-form-item label="版本号" prop="version">
          <el-input
            v-model.trim="form.version"
            placeholder="请输入版本号"
            maxlength="30"
          />
        </el-form-item>
        <el-form-item label="说明" prop="desc">
          <el-input
            v-model.trim="form.desc"
            maxlength="60"
            type="textarea"
            show-word-limit
          />
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">
        取 消
      </el-button>
      <el-button
        type="primary"
        :loading="loading.confirm"
        @click="confirm"
      >
        确 定
      </el-button>
    </div>
  </el-dialog>
</template>

<script>
// import { saveBillCome } from '@/api/audit/receivableManage';
import loadingMixin from '@thales/loading';

export default {
  mixins: [loadingMixin],
  data() {
    return {
      isAdd: true,
      loading: {
        confirm: false,
      },
      visible: false,
      form: {},
      rules: {
        funName: [{ required: true, message: '请输入功能名称', trigger: 'blur' }],
        appName: [{ required: true, message: '请输入应用名称', trigger: 'blur' }],
        flowName: [{ required: true, message: '请输入流程名称', trigger: 'blur' }],
        version: [{ required: true, message: '请输入版本号', trigger: 'blur' }],
        desc: [{ required: true, message: '请输入说明', trigger: 'blur' }],
      },
    };
  },
  computed: {
    title() {
      return this.isAdd ? '新增' : '编辑';
    },
  },
  mounted() {},
  created() {},
  methods: {
    fetchData() {},
    async confirm() {
      await this.$refs.form.validate();
      const params = {
        ...this.form,
      };
      // await saveBillCome(params);
      this.$emit('success', params);
      this.hide();
    },
    cancel() {
      this.hide();
    },
    show() {
      this.isAdd = true;
      this.form = {};
      this.visible = true;
    },
    showEdit(row) {
      this.isAdd = false;
      this.form = {
        ...row,
      };
      this.visible = true;
    },
    hide() {
      this.$refs.form.resetFields();
      this.visible = false;
    },
  },
};
</script>
